import { useEffect, useState } from 'react';

import OrderRequests from '../../../api/order';
import { Order } from '../../../api/order/types';

import Loading from '../../loading';
import OrderRow from '../card';

import style from './style.module.scss';

const OrderList = () => {
    const [orders, setOrders] = useState<Order[]>();
    
    useEffect(() => {
        OrderRequests.orders().then(setOrders).catch(console.error);
        return () => {}
    }, [])

    if (!orders) {
        return <div className={style.container}>
            <Loading />
        </div>
    }
    
    return <div className={style.container}>
        {
            orders.map((order, index) => <OrderRow order={order} key={index} />)
        }
    </div>
}

export default OrderList;