import { useContext } from "react";

import SearchContext from "../../../contexts/search";

import Loading from "../../loading";

import ProductCard from "../card";

import style from './style.module.scss';

interface Props {
    className?: string
}

const ProductsResults = (props: Props) => {
    const { searching, products } = useContext(SearchContext);

    if (searching) return <Loading />

    const styles = [style.container];
    if (props.className) styles.push(props.className)

    return <div className={styles.join(' ')}>
        { products.map((product, index) => <ProductCard product={product} key={index} />) }        
    </div>
}

export default ProductsResults;