import { createContext, ReactNode, useCallback, useEffect, useState } from "react";

import ProductRequests, { Product, SearchFilter, SearchResults } from "../api/product";

interface SearchContextValue {
    searching: boolean
    filter: SearchFilter
    products: Product[]
    search: (filter: { name?: string }) => Promise<SearchResults>
}

const SearchContext = createContext<SearchContextValue>({
    searching: false,
    filter: {},
    products: [],
    search: () => { throw new Error() }
});

interface Props {
    children: ReactNode
}

const SearchProvider = (props: Props) => {
    const [searching, setSearching] = useState(false);
    const [results, setResults] = useState<SearchResults>({
        products: [],
        filter: {}
    });

    const search = useCallback(async (filter: SearchFilter): Promise<SearchResults> => {
        setSearching(true);
        
        let result: SearchResults;

        try {
            result = await ProductRequests.search(filter);
        } catch (e) {
            setSearching(false);
            throw e
        }

        setResults(result);
        setSearching(false);

        return result;
    }, [])

    useEffect(() => { search({}) }, [search])

    return <SearchContext.Provider value={{
        searching,
        filter: results.filter,
        products: results.products,
        search
    }} >
        { props.children }
    </SearchContext.Provider>
}

export default SearchContext;
export { SearchProvider };