import Realtime from "../realtime";
import Requests from "../requests";

import { EventManager } from "../utils/events";

import { Message, NewMessageCMD } from "./types";

class ChatRealtime extends Realtime {

    private static readonly _messageEvent = new EventManager<Message>();

    public static get messageEvent() { return this._messageEvent.register }

    public static start() {
        Realtime.client.on('/message/new', (message: Message) => {
            ChatRealtime._messageEvent.notify(message);
        })

        Realtime.client.emit('/message/follow', {});
    }

    public static close() {
        Realtime.client.emit('/message/unfollow', {});
    }

}

class ChatRequests extends Requests {

    public static async messages(): Promise<Message[]> {
        return await this.fetch({ uri: '/chat' });
    }

    public static async send(cmd: NewMessageCMD): Promise<Message> {
        return await this.fetch<NewMessageCMD, Message>({
            uri: '/chat',
            method: 'POST',
            body: cmd
        })
    }

}

export default ChatRequests;
export {
    ChatRealtime
}