import { createContext, ReactNode, useCallback, useContext, useEffect, useState } from "react";

import CartRequests from "../api/cart";
import { Address, Cart, ItemComplete, SetProductCMD } from "../api/cart/types";

import AuthContext from "./auth";

interface CartContextValue {
    cart: Cart
    items: ItemComplete[]
    setAddress: (address: Address) => Promise<void>
    setItem: (cmd: SetProductCMD) => Promise<void>
    delItem: (id: string) => Promise<void>
    clear: () => Promise<void>
    finish: () => Promise<void>
}

const CartContext = createContext<CartContextValue | undefined>(undefined);

interface Props {
    children: ReactNode
}

const CartProvider = (props: Props) => {
    const { logged } = useContext(AuthContext);
    
    const [data, setData] = useState<{ cart: Cart, items: ItemComplete[] }>();

    const updateCart = useCallback(async () => {
        setData(logged ? await CartRequests.cartHydrated() : undefined);
    }, [logged])

    useEffect(() => {
        updateCart()

        return () => {}
    }, [logged, updateCart])

    const setAddress = async (address: Address) => {
        await CartRequests.setAddress(address);
        await updateCart();
    }
    
    const setItem = async (cmd: SetProductCMD) => {
        await CartRequests.setItem(cmd);
        await updateCart();        
    }

    const delItem = async (id: string) => {
        await CartRequests.delItem(id);
        await updateCart();        
    }

    const clear = async () => {
        await CartRequests.clear();
        await updateCart();        
    }

    const finish = async () => {
        await CartRequests.finish();
        await updateCart();        
    }

    return <CartContext.Provider value={!data ? undefined : {
        cart: data.cart,
        items: data.items,
        setAddress,
        setItem,
        delItem,
        clear,
        finish
    }} >
        { props.children }
    </CartContext.Provider>
}

export default CartContext;
export { CartProvider };