import { useContext } from "react";

import { Product } from "../../../api/product";

import CartContext from "../../../contexts/cart";

import style from './style.module.scss';

interface Props {
    product: Product
}

const ProductCard = (props: Props) => {
    const cart = useContext(CartContext);

    const qunatity = cart?.items.find(item => item.product.id === props.product.id)?.quantity || 0;

    return <div className={style.container}>
        <img
            alt={props.product.name} 
            src={props.product.picture}
            className={style.picture}
        />
        <label className={style.price}>$ {props.product.price}</label>
        <label className={style.name}>{props.product.name}</label>
        {
            cart && <div className={style.cartController}>
                <button disabled={qunatity === 0} className={style.sub} onClick={() => cart.setItem({ product_id: props.product.id, quantity: qunatity - 1})} >-</button>
                <label>{qunatity}</label>
                <button className={style.add} onClick={() => cart.setItem({ product_id: props.product.id, quantity: qunatity + 1})} >+</button>
            </div>
        }
    </div>
}

export default ProductCard;