import Requests from "../requests";

import { AccessToken, SignInParams, SignUpParams, UserProfile } from "./types";

class AuthRequests extends Requests {

    public static async signin(params: SignInParams): Promise<void> {
        const token = await this.fetch<SignInParams, AccessToken>({
            uri: '/auth/signin', 
            method: 'POST',
            body: params
        });

        Requests.setToken(token.access);
    }

    public static async signup(params: SignUpParams): Promise<void> {
        const token = await this.fetch<SignUpParams, AccessToken>({
            uri: '/auth/signup', 
            method: 'POST',
            body: params
        });

        Requests.setToken(token.access);
    }

    public static async profile(): Promise<UserProfile> {
        return await this.fetch<void, UserProfile>({ uri: '/user' });
    }

    public static async logout(): Promise<void> {
        await this.fetch({
            uri: '/auth/logout', 
            method: 'POST'
        });

        Requests.setToken(undefined);
    }

}

export default AuthRequests;
export type {
    SignInParams,
    SignUpParams,
    UserProfile
}