import Requests from "../requests";

import { Order } from "./types";

class OrderRequests extends Requests {

    public static async orders(): Promise<Order[]> {
        return await this.fetch<void, Order[]>({ uri: '/order' });
    }

    public static async order(id: string): Promise<Order> {
        return await this.fetch<void, Order>({ uri: `/order/${id}` });
    }

}

export default OrderRequests;