import Requests from "../requests";

import { Product, SearchFilter, SearchResults } from "./types";

class ProductRequests extends Requests {

    public static async search(filter: SearchFilter): Promise<SearchResults> {
        const products = await this.fetch<SearchFilter, Product[]>({
            uri: '/product/search', 
            method: 'POST',
            body: filter
        });
    
        return { products, filter };
    }

}

export default ProductRequests;
export type {
    Product,
    SearchFilter,
    SearchResults
}