import { useContext, useEffect, useState } from "react";
import { Address } from "../../../api/cart/types";

import CartContext from "../../../contexts/cart";
import Loading from "../../loading";

import style from './style.module.scss';

const CartAside = () => {
    const cart = useContext(CartContext);
    
    const [details, setDetails] = useState(false);
    const [finishing, setFinishing] = useState(false);
    const [updatingAddress, setUpdatingAddress] = useState(false);

    const [address, setAddress] = useState<Address | undefined>(undefined);

    useEffect(() => {
        if (cart) setAddress(cart.cart.address);

        return () => {}
    }, [cart])

    if (!cart) return <></>;

    const updateAddress = () => {
        if (!address) return;

        setUpdatingAddress(true);

        cart.setAddress(address)
            .finally(() => setUpdatingAddress(false));
    }

    const finalize = () => {
        setFinishing(true);
        cart.finish().finally(() => setFinishing(false));
    }

    return <div className={style.container}>
        <div className={style.header}>
            <label>Carrito</label>
            <button onClick={() => setDetails(!details)}>Mostrar Detalles</button>
        </div>
        {
            details && <div className={style.content}>
            <div className={style.card}>
                <label className={style.title} >Items</label>
                <div className={style.cardContent}>
                    {
                        cart.items.length === 0 && <label>No hay items en el carrito</label>
                    }
                    {
                        cart.items.map((item, index) => {
                            return <div key={index} className={style.item} >
                                <label>{item.quantity}</label>
                                <b>{item.product.name}</b>
                                <label>$ {item.total}</label>
                            </div>
                        })
                    }
                </div>
            </div>
            <div className={style.card}>
                <label className={style.title} >Direccion</label>
                <div className={style.cardContent}>
                    <input
                        placeholder={"Ciudad"}
                        value={address?.city || ''}
                        onChange={e => address && setAddress({ ...address, city: e.currentTarget.value })}
                    />
                    <input
                        placeholder={"Codigo ZIP"}
                        value={address?.zip_code || ''}
                        onChange={e => address && setAddress({ ...address, zip_code: e.currentTarget.value })}
                    />
                    <input
                        placeholder={"Street"}
                        value={address?.street || ''}
                        onChange={e => address && setAddress({ ...address, street: e.currentTarget.value })}
                    />
                    <input
                        placeholder={"Numero"}
                        value={address?.number || ''}
                        onChange={e => address && setAddress({ ...address, number: e.currentTarget.value })}
                    />
                    <input
                        placeholder={"Indicaciones"}
                        value={address?.indications || ''}
                        onChange={e => address && setAddress({ ...address, indications: e.currentTarget.value })}
                    />
                    <button onClick={updateAddress} >{ updatingAddress ? <Loading /> : 'Guardar' }</button>
                </div>
            </div>
            <div className={style.card}>
                <label className={style.title} >Finalizar</label>
                <div className={style.cardContent}>
                    <label className={style.total} >total: $ {cart.cart.total}</label>
                    <button onClick={finalize} >{ finishing ? <Loading /> : 'Finalizar' }</button>
                </div>
            </div>
        </div>
        }
    </div>
}

export default CartAside;