import { io, Socket } from "socket.io-client";

import { EventManager } from "./utils/events";

class Realtime {

    private static host: string = 'http://localhost:5000'
    private static _token: string | undefined;
    private static events = {
        token: new EventManager<string | undefined>()
    }
    protected static client: Socket = io(Realtime.host);

    public static get on() {
        return { token: this.events.token.register }
    }

    public static get token() { return this._token }

    public static setToken(token?: string) {
        this._token = token;

        if (token) {
            this.client.disconnect();
            this.client = io(Realtime.host, { auth: { token: token } });
        }

        this.events.token.notify(token);
    }

}

export default Realtime;