import { useContext } from "react"
import { useHistory } from 'react-router-dom'

import AuthContext from "../../../contexts/auth"

import style from './style.module.scss';

const UserLogged = () => {
    const { logout, profile } = useContext(AuthContext);

    return <div className={style.container}>
        { profile && <label>{profile.name}</label> }
        <button onClick={logout} className={style.logoutButton} >Salir</button>
    </div>
}

const UserNotLogged = () => {
    const history = useHistory()

    return <div className={style.container}>
        <button className={style.ingressButton} onClick={() => history.push('/ingresar')}>Ingresar</button>
        <button className={style.registerButton} onClick={() => history.push('/registrar')}>Registrar</button>
    </div>
}

const UserHeader = () => {
    const { logged } = useContext(AuthContext);

    return logged ? <UserLogged /> : <UserNotLogged />
}

export default UserHeader;