import { ReactNode } from "react";

import { AuthProvider } from "./auth";
import { CartProvider } from "./cart";
import { SearchProvider } from "./search";

interface Props {
    children: ReactNode
}

const RootContext = (props: Props) => {
    return <AuthProvider>
        <SearchProvider>
            <CartProvider>
                {props.children}
            </CartProvider>
        </SearchProvider>
    </AuthProvider>;
}

export default RootContext;