import { useState } from 'react';

import { Order } from '../../../api/order/types';

import style from './style.module.scss';

interface Props {
    order: Order
}

const OrderRow = (props: Props) => {    
    const [details, setDetails] = useState(false);
    
    return <div className={style.container}>
        <div className={style.header}>
            <label>N: {props.order.id.slice(0, 5)} ({props.order.state})</label>
            <label className={style.total} >$ {props.order.total}</label>
            <button onClick={() => setDetails(!details)}>Mostrar Detalles</button>
        </div>
        {
            details && <div className={style.content}>
            <div className={style.card}>
                <label className={style.title} >Items</label>
                <div className={style.cardContent}>
                    {
                        props.order.items.length === 0 && <label>No hay items en la orden</label>
                    }
                    {
                        props.order.items.map((item, index) => {
                            return <div key={index} className={style.item} >
                                <label>{item.quantity}</label>
                                <b>{item.name}</b>
                                <label>$ {item.total}</label>
                            </div>
                        })
                    }
                </div>
            </div>
            <div className={style.card}>
                <label className={style.title} >Direccion</label>
                <div className={style.cardContent}>
                    { `${props.order.address.city}, ${props.order.address.street} ${props.order.address.number}` }
                </div>
            </div>
        </div>
        }
    </div>
}

export default OrderRow;