import Requests from "../requests";

import ProductRequests from "../product";

import { Address, Cart, ItemComplete, SetProductCMD } from "./types";

class CartRequests extends Requests {

    public static async cart(): Promise<Cart> {
        return await this.fetch({ uri: '/cart' });
    }

    public static async cartHydrated(): Promise<{ cart: Cart, items: ItemComplete[] }> {
        const cart = await this.fetch<void, Cart>({ uri: '/cart' });

        const result = await ProductRequests.search({
            ids: cart.items_ref.map(item => item.product_id)
        })
        const productsMap = Object.fromEntries(result.products.map(product => [product.id, product]));

        const items = cart.items_ref.map(item => {
            return {
                product: productsMap[item.product_id],
                quantity: item.quantity,
                total: item.total
            }
        });

        return { cart, items };
    }

    public static async clear(): Promise<Cart> {
        return await this.fetch<void, Cart>({
            uri: '/cart/clear',
            method: 'POST'
        })
    }

    public static async setAddress(address: Address): Promise<Cart> {
        return await this.fetch<Address, Cart>({
            uri: '/cart/address',
            method: 'POST',
            body: address
        })
    }

    public static async setItem(cmd: SetProductCMD): Promise<Cart> {
        return await this.fetch<SetProductCMD, Cart>({
            uri: '/cart/products',
            method: 'POST',
            body: cmd
        })
    }

    public static async delItem(id: string): Promise<Cart> {
        return await this.fetch<string, Cart>({
            uri: `/cart/product/${id}`,
            method: 'DELETE'
        })
    }

    public static async finish(): Promise<void> {
        await this.fetch<SetProductCMD, Cart>({
            uri: '/cart/finish',
            method: 'POST'
        })
    }

}

export default CartRequests;