import { useContext } from 'react';
import { NavLink } from 'react-router-dom';

import AuthContext from '../../contexts/auth';

import Logo from '../logo';
import UserHeader from '../user/header';

import style from './style.module.scss';

interface Props {
    className?: string
}

const Header = (props: Props) => {
    const { logged } = useContext(AuthContext);

    const styles = [style.container];
    if (props.className) styles.push(props.className);

    return <header className={styles.join(' ')}>
        <div className={style.content}>
            <div className={style.left}>
                <Logo />
            </div>
            <div className={style.center}>
                {
                    logged && <>
                        <NavLink className={style.link} activeClassName={style.selected} exact to={"/"} >Shop</NavLink>
                        <NavLink className={style.link} activeClassName={style.selected} to={"/ordenes"} >Ordenes</NavLink>
                        <NavLink className={style.link} activeClassName={style.selected} to={"/chat"} >Chat</NavLink>                    
                    </>
                }
            </div>
            <div className={style.right}>
                <UserHeader />
            </div>
        </div>
    </header>
}

export default Header;