import Realtime from "./realtime";
import { EventManager } from "./utils/events";

interface Props<P> {
    uri: string
    method?: 'GET' | 'POST' | 'PUT' | 'DELETE',
    body?: P
}

interface Response<R> {
    result: R,
    error?: {
        message: string
    }
}

class Requests {

    private static host: string = 'http://localhost:5000'
    private static _token: string | undefined;
    private static events = {
        token: new EventManager<string | undefined>()
    }

    public static get on() {
        return { token: this.events.token.register }
    }

    public static get token() { return this._token }

    public static setToken(token?: string) {
        this._token = token;

        Realtime.setToken(token);

        this.events.token.notify(token);
    }

    protected static async fetch<P, R>(props: Props<P>): Promise<R> {
        const response = await fetch(`${this.host}${props.uri}`, {
            headers: this.headers(),
            method: props.method || 'GET',
            body: props.body ? JSON.stringify(props.body) : undefined
        });

        const data: Response<R> = await response.json();
        if (data.error) throw new Error(data.error.message);
        
        return data.result;
    }

    private static headers() {
        const headers: any = {
            'Content-Type': 'application/json'
        };

        if (this._token) headers.Authorization = `Basic ${this._token}`;

        return headers;
    }

}

export default Requests;