import { FormEvent, useEffect, useState } from 'react';

import ChatRequests, { ChatRealtime } from '../../../api/chat';
import { Message } from '../../../api/chat/types';

import Loading from '../../loading';

import style from './style.module.scss';

const ChatContainer = () => {
    const [messages, setMessages] = useState<Message[]>();
    const [message, setMessage] = useState('');
    const [lading, setLoading] = useState(false);

    useEffect(() => {
        const onMessage = async (message: Message) => {
            setMessages(messages => [ ...(messages || []), message ]);
        }

        ChatRealtime.messageEvent.listen(onMessage);
        ChatRealtime.start();

        ChatRequests.messages()
            .then(setMessages)
            .catch(console.error)

        return () => {
            ChatRealtime.messageEvent.remove(onMessage);
            ChatRealtime.close();
        }
    }, [])

    const send = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        setLoading(true)
        
        ChatRequests.send({ body: message })
            .finally(() => {
                setLoading(false);
                setMessage('');
            })
    }

    return <div className={style.container}>
        <div className={style.messages}>
            {
                (!messages) ? <Loading /> : messages.map((message, index) => {
                    const styleBy = message.by === 'client' ? style.client : style.seller;

                    return <div key={index} className={`${style.message} ${styleBy}`}>
                        <label className={style.name}>{message.user.name}</label>
                        <label className={style.body}>{message.body}</label>
                    </div>
                })
            }
        </div>
        <form onSubmit={send} className={style.form}>
            <input placeholder={"mensaje"} value={message} onChange={e => setMessage(e.currentTarget.value)} />
            <button>{ lading ? <Loading /> : 'Enviar'}</button>
        </form>
    </div>
}

export default ChatContainer;