import { useContext } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom'

import AuthContext from './contexts/auth';

import RootContext from './contexts';

import Header from './components/header';
import Search from './components/search';
import ProductsResults from './components/products/results';
import CartAside from './components/cart/aside';
import OrderList from './components/order/list';
import ChatContainer from './components/chat/container';
import SignInForm from './components/user/signin';
import SignUpForm from './components/user/signup';

import style from './app.module.scss';

const Panel = () => {
  return <>
    <CartAside />
    <Search />
    <ProductsResults className={style.products} />
  </>
}

const Routes = () => {
  const { logged } = useContext(AuthContext);
  
  return <Switch>
    {
      logged ? <>
          <Route exact path={'/ordenes'} component={OrderList} />
          <Route exact path={'/chat'} component={ChatContainer} />
          <Route path={"/"} exact component={Panel} />
        </> : <>
          <Route exact path={'/ingresar'} component={SignInForm} />
          <Route exact path={'/registrar'} component={SignUpForm} />
          <Route path={"/"} exact component={Panel} />
        </>
    }
  </Switch>
}

const App = () => { 
  return <div className={style.root}>
    <RootContext>
      <BrowserRouter>
        <Header className={style.header} />
        <div className={style.content}>
          <Routes />
        </div>
      </BrowserRouter>
    </RootContext>
  </div>
}

export default App;