import { FormEvent, useContext, useEffect, useState } from 'react';
import { useHistory } from 'react-router';

import { SignUpParams } from '../../../api/auth';

import AuthContext from '../../../contexts/auth';

import Loading from '../../loading';

import style from './style.module.scss';

const SignUpForm = () => {
    const { logged, signup } = useContext(AuthContext);

    const history = useHistory();

    const [params, setParams] = useState<SignUpParams>({
        name: '',
        lastname: '',
        phone: '',
        email: '',
        password: ''
    })
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string>();

    useEffect(() => {
        if (logged) history.push('/');
        return () => {}
    }, [logged, history])

    const register = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        setLoading(true);

        signup(params)
            .catch(err => setError(err.message))
            .finally(() => setLoading(false));
    }
    
    return <div className={style.container}>
        {
            loading ? <Loading /> : <>
                <form onSubmit={register} className={style.form} >
                    <input placeholder={"Nombre"} value={params.name} onChange={e => setParams({ ...params, name: e.currentTarget.value })} />
                    <input placeholder={"Apellido"} value={params.lastname} onChange={e => setParams({ ...params, lastname: e.currentTarget.value })} />
                    <input placeholder={"Telefono"} value={params.phone} onChange={e => setParams({ ...params, phone: e.currentTarget.value })} />
                    <input placeholder={"Email"} value={params.email} onChange={e => setParams({ ...params, email: e.currentTarget.value })} />
                    <input placeholder={"Contrasea"} value={params.password} onChange={e => setParams({ ...params, password: e.currentTarget.value })} />
                    <button>Registrar</button>
                </form>
                { error && <label>{error}</label> }
                <button className={style.back} onClick={() => history.push('/')} >Volver</button>
            </>
        }
    </div>
}

export default SignUpForm;