import { createContext, ReactNode, useEffect, useState } from "react";

import AuthRequests, { SignInParams, SignUpParams, UserProfile } from "../api/auth";

import Requests from "../api/requests";

interface AuthContextValue {
    logged: boolean
    profile?: UserProfile
    signin: (params: SignInParams) => Promise<void>
    signup: (params: SignUpParams) => Promise<void>
    logout: () => Promise<void>
}

const AuthContext = createContext<AuthContextValue>({
    logged: false,
    profile: undefined,
    signin: () => { throw new Error() },
    signup: () => { throw new Error() },
    logout: () => { throw new Error() }
});

interface Props {
    children: ReactNode
}

const AuthProvider = (props: Props) => {
    const [token, setToken] = useState<string>();
    const [profile, setProfile] = useState<UserProfile>();

    useEffect(() => {
        const newToken = (token?: string) => {
            if (token) {
                if (window?.localStorage) window.localStorage.setItem('token', token);

                AuthRequests.profile()
                    .then(setProfile)
                    .catch(console.error)
            } else {
                if (window?.localStorage) window.localStorage.removeItem('token');

                setProfile(undefined);
            }

            setToken(token);
        }

        Requests.on.token.listen(newToken);
        return () => { Requests.on.token.remove(newToken) }
    }, [])

    useEffect(() => {
        if (window?.localStorage) {
            const token = window.localStorage.getItem('token') || undefined;
            Requests.setToken(token);
        }

        return () => {}
    }, [])

    const signin = async (params: SignInParams): Promise<void> => {
        return AuthRequests.signin(params)
    }

    const signup = async (params: SignUpParams): Promise<void> => {
        return AuthRequests.signup(params)
    }

    const logout = async (): Promise<void> => {
        return AuthRequests.logout()
    }

    return <AuthContext.Provider value={{
        logged: !!token,
        profile,
        signin,
        signup,
        logout
    }} >
        { props.children }
    </AuthContext.Provider>
}

export default AuthContext;
export { AuthProvider };